.header {
    height: 240px ;
    background: no-repeat center url(../images/imgBook.jpg);
    background-size: cover;
    color: white;
    text-align: center;
    position: relative;
}

.header::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,.5);
    z-index: 1;
  }

.header__heading {
    margin: 0;
    padding-top: 40px;
    position: relative;
    z-index: 2;
}
